const NumberForm = ({ query }) => {

    const filterList = [
        {
            name: 'SG Number',
            itemKey: 'numbers'
        },
        {
            name: 'MY Number',
            itemKey: 'mynumbers'
        }

    ]
    const [selectedIndex, setSelectedIndex] = useState(0)
    const [data, setData] = useState([])
    const [_query, setQuery] = useState(query)
    const [isLoading, setIsLoading] = useState(false)
    const [itemKey, setItemKey] = useState('')
    const search = async (query) => {
        setIsLoading(true)
        const response = await Parse.Cloud.run('searchNumbers', {
            query,
            itemKey: filterList[selectedIndex]?.itemKey
        })
        setData(response)
        setIsLoading(false)
        return response
    }

    useEffect(async () => {
        await search(query)
    }, [])

    return <Box>
        eee
        < Box display="flex" justifyContent={"center"} alignItems={"center"} flexDirection={"column"} gap="5px" >

            <Filter
                handleMenuItemClick={(e, index, item) => { setSelectedIndex(index) }}
                selectedIndex={selectedIndex}
                list={filterList}
            />
            <TextField
                //// type='tel'
                autoFocus={true}
                value={_query}
                onChange={e => {
                    setQuery(e.target.value)
                }}
            />

            <Button
                variant='contained'
                onClick={async () => {
                    await search(_query)
                }}
            >
                Find
            </Button>
        </Box >
        <br />
        <NumberConsole
            dataArr={data}
            pageSize={4}
            hideDl={true}
            isLoading={isLoading}
        />
        <Box display={"flex"} alignItems={"center"} justifyContent={"center"}>
            <h2><a style={{ color: '#25D366' }} href={`https://api.whatsapp.com/send?phone=6587413159&text=I want to see numbers similar to ${query || ''}`}>Whatsapp</a> us to see more result or get the number.</h2>
        </Box>
    </Box >


}

